function Message({ message }) {
  return (
    <div className="message">
      <p>
        <span>{message.senderName}: </span>
        {message.message.split(':').map((word, i) => (
          <Fragment key={i}>
            {word[0] === '<' && word[word.length - 1] === '>' ? (
              <Emote name={word.slice(1, -1)} />
            ) : (
              word
            )}
          </Fragment>
        ))}
      </p>
    </div>
  );
}

