import {
  pepega,
  slayer,
  trollge,
  ahha,
  okayeg,
  kiryuHA,
  PepeChill,
  Pog,
  SadCat,
  widepeepoHappy,
  widepeepoSad,
} from './emotes';

export default function Emote({ name }) {
  let image;
  switch (name) {
    case 'pepega':
      image = pepega;
      break;
    case 'slayer':
      image = slayer;
      break;
    case 'trollge':
      image = trollge;
      break;
    case 'ahha':
      image = ahha;
      break;
    case 'okayeg':
      image = okayeg;
      break;
    case 'kiryuHA':
      image = kiryuHA;
      break;
    case 'PepeChill':
      image = PepeChill;
      break;
    case 'Pog':
      image = Pog;
      break;
    case 'SadCat':
      image = SadCat;
      break;
    case 'widepeepoHappy':
      image = widepeepoHappy;
      break;
    case 'widepeepoSad':
      image = widepeepoSad;
      break;
    default:
      image = null;
      break;
  }
  return image && <img className="emote" src={image} alt={name} />;
}
