import { useState, useEffect, useRef } from 'react';
import Message from './Message';
import {
  selectHMSMessages,
  useHMSActions,
  useHMSStore,
} from '@100mslive/react-sdk';
import { selectPeers } from '@100mslive/react-sdk';
import { AiFillSmile } from 'react-icons/ai';
import data from '@emoji-mart/data';
import Picker from '@emoji-mart/react';
import { custom } from './emotes/emotes';

function ChatNdParticipants() {
  const [selectedOption, setSelectedOption] = useState('chat');
  const [message, setMessage] = useState('');
  const [showEmotes, setShowEmotes] = useState(false);
  const messages = useHMSStore(selectHMSMessages);
  const container = useRef();
  const hmsActions = useHMSActions();
  const peers = useHMSStore(selectPeers);

  const handleSubmit = e => {
    e.preventDefault();
    if (message.trim() === '') {
      return;
    }

    console.log(message);
    hmsActions.sendBroadcastMessage(message);
    setMessage('');
  };

  const Scroll = () => {
    const { offsetHeight, scrollHeight, scrollTop } = container.current;
    if (scrollHeight <= scrollTop + offsetHeight + 150) {
      container.current?.scrollTo(0, scrollHeight);
    }
  };

  const addEmoji = e => {
    let emoji;
    if (e.native) {
      emoji = e.native;
    } else {
      emoji = e.shortcodes;
    }
    setMessage(prevState => prevState + emoji);
  };

  const handleChange = e => {
    setMessage(e.target.value);
  };

  useEffect(() => {
    Scroll();
  }, [messages]);

  return (
    <div className="rightBox">
      <div className="rightBox__head">
        <span
          onClick={() => setSelectedOption('chat')}
          className={selectedOption === 'chat' ? 'selected' : ''}
        >
          Chat
        </span>
        <span
          className={selectedOption === 'participants' ? 'selected' : ''}
          onClick={() => setSelectedOption('participants')}
        >
          Participants
        </span>
      </div>
      <div className="rightBox__optionView">
        {selectedOption === 'chat' && (
          <>
            <div className="rightBox__chats" ref={container}>
              {messages.map(msg => (
                <Message key={msg.id} message={msg} />
              ))}
            </div>
            <div style={{ paddingLeft: 10, paddingRight: 10 }}>
              <form
                name="send-message"
                onSubmit={handleSubmit}
                className="messageSend"
              >
                <div>
                  <input
                    onChange={handleChange}
                    value={message}
                    placeholder="Write your message"
                  />

                  <button
                    type="button"
                    className="emotes"
                    onClick={() => setShowEmotes(prevState => !prevState)}
                  >
                    <AiFillSmile />
                  </button>
                  {showEmotes && (
                    <div className="emoteField">
                      <Picker
                        onEmojiSelect={addEmoji}
                        data={data}
                        className="emoteField"
                        navPosition="top"
                        custom={custom}
                      />
                    </div>
                  )}
                </div>
              </form>
            </div>
          </>
        )}
        {selectedOption === 'participants' && (
          <div className="rightBox__participants">
            {peers.map(peer => (
              <div className="rightBox__participant">
                {peer.name}
                <p>{peer.roleName}</p>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default ChatNdParticipants;
